import Vue from 'vue'
import Router from 'vue-router'
import index from 'views/index/index'
import home from 'views/Home/index'
import user from 'views/User/index'
import video from 'views/Video/index'
import msg from 'views/Msg/index'
import mall from 'views/Mall/index'
import feedback from 'views/Feedback/index'
import record from 'views/Record/index'
import system from 'views/System/index'
import headline from 'views/Headline/index'
import article from 'views/Article/index'
import search from 'views/Search/index'


Vue.use(Router)

export const router = new Router({
  routes: [
    { 
        path: '/', 
        name: '', 
        meta: { page: true},
        component: index,
        redirect: '/home',
        children: [
            { path: 'home', component: home, name: '' }
        ]
    },
    { 
      path: '/video',  
      meta: { page: true},
      component: index,
      redirect: '/',
      children: [
          { path: '/', component: video, name: '' }
      ]
    },
    { 
        path: '/headline',  
        meta: { page: true},
        component: index,
        redirect: '/',
        children: [
            { path: '/', component: headline, name: '' }
        ]
    },
    { 
        path: '/user', 
        meta: { page: true}, 
        component: user,
        name: ''
    },
    { 
        path: '/search', 
        name: '', 
        component: search
    },
    { 
        path: '/article/:id', 
        name: '', 
        meta: { slide: 1 },
        component: article
    },
    { 
        path: '/msg', 
        name: '', 
        meta: { 
            slide: 1 ,
            login: true
        },
        component: msg
    },
    { 
        path: '/mall', 
        name: '', 
        meta: { 
            slide: 1 ,
            login: true
        },
        component: mall
    },
    { 
        path: '/feedback', 
        name: '', 
        meta: { 
            slide: 1 ,
            login: true
        },
        component: feedback
    },
    { 
        path: '/record', 
        name: '', 
        meta: { 
            slide: 1 ,
            login: true
        },
        component: record
    },
    { 
        path: '/system', 
        name: '', 
        meta: { 
            slide: 1 ,
            login: true
        },
        component: system
    },
  ]
})



